import '../styles/home.scss';

export const query = graphql`
  query {
    craft {
      entries(section: home) {
        ... on Craft_Home {
          seomatic {
            metaTagContainer
            metaLinkContainer
            metaTitleContainer
            metaScriptContainer
            metaJsonLdContainer
          }
          homeBanner {
            ... on Craft_HomeBannerHomeBannerSlide {
              slideTagline
              slideClientName
              slideBannerImage {
                url
              }
              slideVideo {
                url
              }
              slideAmbientBackgroundVideo
              slideLinkToStory {
                slug
              }
            }
          }
          homeVimeo
          homeIntroText
          homeSubText
          projectsFeed {
            ... on Craft_Projects {
              previewTile {
                url
                width
                height
                ... on Craft_UploadsVolume {
                  optimizedimages {
                    srcset
                    colorPalette
                  }
                }
              }
              title
              slug
              description {
                content
              }
            }
          }
        }
      }
    }
  }  
`

